* {
    box-sizing: border-box; }

body {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    color: #BCBCBC;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: -0.01em; }
.title {
    font-family: 'adineuePRO-Black', sans-serif;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-size: 24px;
    line-height: 120%;
    color: #231F20; }
button {
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    padding: 15px 41px;
    border-radius: 4px;
    border: none;
    font-weight: 400;
    background-color: #032186;
    color: #ffffff;
    text-decoration: none;
    border-color: transparent;
    font-size: 14px;
    letter-spacing: .1em;
    cursor: pointer;
    text-transform: uppercase;
    transition: all .3s;
    &:hover {
        background-color: #231F20; } }

h2 {
    font-family: 'adineuePRO-Black', sans-serif;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-size: 44px;
    line-height: 120%;
    color: #231F20; }
h3 {
    font-family: 'adineuePRO-Black', sans-serif;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-size: 18px;
    color: #231F20; }

h5 {
    font-family: 'adineuePRO-Black', sans-serif;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-size: 18px;
    color: #231F20; }
.header {
    border-top: 8px solid #231F20; }
.container {
    max-width: 1124px;
    margin: 10 auto;
    padding: 0 32px; }

.header_inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 89px; }

.logo_img {
    width: 272px;
    margin-top: 8px;
    color: #231F20; }

.nav_wraper {
    margin-left: auto; }

.menu {

    margin-top: 24px;
    &_list {
        display: flex;
        justify-content: flex-end; }
    &_list-item {
        margin-left: 18px; }
    &_list-link_phone {
        color: #231F20;
        font-size: 14px;
        font-weight: 500;
        text-decoration: none;
        &:hover {
            text-decoration: none;
            color: #BCBCBC; } }
    &_list-link {
        color: #231F20;
        font-size: 14px;
        font-weight: 500;
        padding: 6px 8px;
        border: 1px solid #231F20;
        border-radius: 2px;
        text-decoration: none;
        &:hover {
            text-decoration: none;
            color: #BCBCBC; } }
    &_list-link_btn {

        font-size: 14px;
        font-weight: 500;
        padding: 6px 8px;
        border: 1px solid #231F20;
        border-radius: 2px;
        text-decoration: none;
        border-color: transparent;
        background-color: #231F20;
        color: #fff;
        transition: all .3s;
        &:hover {
            text-decoration: none;
            background-color: #032186;
            color: #fff; } } }
.user-nav {
    margin: 16px auto;
    &_link {
        font-family: 'adineuePRO-Black', sans-serif;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: #231F20;
        font-size: 16px;
        text-decoration: none;
        margin-left: 14px;
        &:hover {
            text-decoration: none;
            color: #BCBCBC; } } }

/** -------------HERO SECTION---------- **/

.top {
    display: block;
    background-image: url('../img/hero/hero-0.jpg');
    background-repeat: no-repeat;
    background-position: center;

    &_inner {
        padding: 108px 0 80px 0; }

    &_title {
        max-width: 460px;
        margin-bottom: 30px;
        font-family: 'adineuePRO-Black', sans-serif;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        font-size: 44px;
        line-height: 120%;
        color: #231F20; }
    &_text {
        max-width: 360px;
        margin-bottom: 45px;
        font-weight: 300;
        font-size: 18px;
        line-height: 28px;
        color: #231F20; } }

/** ---------------ANATOMY---------- **/

.anatomy {
    padding-top: 2px;
    &_item-banner {
        left: 0;
        width: 246px; } }
.col-md-4 {
    width: 268px; }

/** ---------------TEAM---------- **/
.team {
    &_inner {

        margin-top: 20px;
        position: relative;
        padding: 280px 0 264px; }

    &_box {
        text-align: center;
        max-width: 542px;
        margin: 0 auto;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        font-size: 44px;
        color: #231F20; }
    &_title {
        margin-bottom: 22px; }

    &_item {
        position: absolute;
        width: 204px;
        height: 204px; }
    &_item-1 {
        left: 300px;
        top: 0px; }

    &_item-2 {
        right: 300px;
        top: 0px; }

    &_item-3 {
        left: 170px;
        top: 208px; }

    &_item-4 {
        right: 170px;
        top: 208px; }

    &_item-5 {
        left: 300px;
        top: 410px; }
    &_item-6 {
        right: 300px;
        top: 410px; }

    &_item-img {
        width: 204px;
        height: 204px;
        border-radius: 50%;
        overflow: hidden;
        object-fit: cover; }

    &_item-text-right {
        /** display: inline-block **/
        background: #FFFFFF;
        padding: 14px 20px 15px;
        color: #2f281e;
        box-shadow: 0px 2px 15px rgba(23, 58, 86, 0.1);
        border-radius: 26px;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        position: absolute;
        /** width: 281px **/
        top: 140px;
        right: 155px;
        white-space: nowrap  /** что-бы текст не переносится **/; }
    &_item-text-right {
        &::after {
            content: "";
            width: 17px;
            height: 17px;
            border-radius: 100%;
            position: absolute;
            background-color: #fff;
            top: -7px;
            right: 30px; }

        &::before {
            content: "";
            width: 9px;
            height: 9px;
            border-radius: 100%;
            position: absolute;
            background-color: #fff;
            top: -20px;
            right: 14px; } }
    &_item-text-left {
        display: inline-block;
        background: #FFFFFF;
        padding: 14px 20px 15px;
        color: #2f281e;
        box-shadow: 0px 2px 15px rgba(23, 58, 86, 0.1);
        border-radius: 26px;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        position: absolute;
        width: 272px;
        top: 140px;
        right: auto;
        left: 156px; }

    &_item-text-left {
        &::after {
            content: "";
            width: 17px;
            height: 17px;
            border-radius: 100%;
            position: absolute;
            background-color: #fff;
            top: -7px;
            right: auto;
            left: 30px; }

        &::before {
            content: "";
            width: 9px;
            height: 9px;
            border-radius: 100%;
            position: absolute;
            background-color: #fff;
            top: -18px;
            left: 14px;
            right: auto; } } }

