/** ---------------FEATURES---------- **/
.feature {

    background-color: #E5E4EA;
    &_title {
        margin-top: 48px;
        font-size: 42px;
        line-height: 42px;

        margin-bottom: 48px; }
    &_text {
        margin-bottom: 55px; } }
.slider {
    position: relative;
    margin: 24px -20px;
    &_item {
        padding: 0 20px; }
    &_item-img {
        width: 252px;
        height: 328px;
        object-fit: contain;
        margin-bottom: 25px;
        background-color: #ffffff;
        background-repeat: no-repeat;
        background-size: 200px;
        &:hover {
            color: #ff9900; } } }
.slick-dots {
    list-style-type: none;
    display: flex;
    justify-content: center; }
.slick-dots button {
    width: 16px;
    height: 16px;
    border-radius: 100%;
    border: none;
    background-color: #efefef;
    font-size: 0;
    padding: 0;
    margin: 0 5px; }
.slick-dots .slick-active button {
    background-color: #2f281e; }

